.ui.huge.inverted.button.mainCTABtn{
color: white !important;
 font-size: calc(10px + 2vmin);
text-transform: uppercase;
background: none;
padding: 20px;
border: 1px solid white !important;
border-radius: 6px;
display: inline-block;
transition: all 0.3s ease 0s;
max-width:70%;
min-width:50%;
}

.ui.huge.inverted.button.mainCTABtn:hover {
color: white !important;
border-radius: 50px;
border-color: #494949 !important;
transition: all 0.3s ease 0s;
}

.ui.huge.inverted.button.mainCTABtn:active {
text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
-webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
-moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
transition: all 0.4s ease 0s;
}

.ui.huge.inverted.button.mainCTABtn > .iconContainer{
opacity: 0;
transition: all 0.3s ease 0s;
}


.ui.huge.inverted.button.mainCTABtn:hover > .iconContainer{
transform: translateX(10px)!important;
opacity: 1;
transition: all 0.3s ease 0s;
}

.ui.huge.inverted.button.mainCTABtn:hover > .buttonText{
transform: translateY(10px)!important;
transition: all 0.3s ease 0s;
}

.cta-section{
  background-image:linear-gradient(45deg,  rgba(0,26,255,.7) 1%,rgba(98,38,183,.7) 30%,rgba(140,40,99,.7) 69%,rgba(250,34,52,.7) 100%),url("./images/world-background.jpg") !important;
  background-size: cover;
  background-position:center !important;
  color:white;
}
.border-diagonal{
    position: relative;
    width: 100%;
    overflow: visible;
    height: 250px;
    margin-top: -250px;
}
.extra-border-outer{
  overflow: hidden;
    height: 100%;
    width: 100%;
    position: absolute;
}

.extra-border-inner-tilt-down{
    top: 250px;
  right: 0;
  width: 110%;
  height: 100%;
  -webkit-transform-origin: right top;
  transform-origin: right top;
  -webkit-transform: rotate(5deg);
  transform: rotate(5deg);
  position: absolute;
}
.extra-border-inner-tilt-up{
    top: 250px;
  right: auto;
  left:0;
  width: 110%;
  height: 100%;
  -webkit-transform-origin: left top;
  transform-origin: left top;
  -webkit-transform: rotate(-5deg);
  transform: rotate(-5deg);
  position: absolute;
}
.extra-border-inner-no-tilt{
    top: 250px;
  right: 0;
  width: 110%;
  height: 100%;
  position: absolute;
}
.mobile-overlay{
  height:100vh; 
  width:100%; 
  overflow:hidden; 
  position:fixed;
  z-index:90;
  background:black;
  transform:scale(0.1);
  opacity:0;
  transition:.3s linear 0.1s ;
  border-radius:50% 50%;
  right: -134px;
  top: -274px;
  pointer-events: none;

}


.mobile-overlay-open{
  opacity:1;
  background:black;
  transform: scale(5);
  transition:.3s linear .1s;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes Mobile-Menu-Item-Appear {
  from{
transform: translate(-31px,0px);
    opacity: 0;
  }
  to{
    transform: translate(0px,0px);
    opacity: 1;
  }
}


